/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* actions */
import * as layoutActions from '@src/states/layout/actions';
/* form */
import { LuxulForm, LuxulInput, LuxulSelect, LuxulToggle, LuxulHint, validations } from '@src/components/form/LuxulForm';

/*
	form
*/
class PageComponent extends React.Component {

	constructor() {
		super();
		this.state = {};
	}

	render() {
		const Box_editProfile = {};
		Box_editProfile.title = 'Modify SSID';
		Box_editProfile.theme = 'global';
		Box_editProfile.content = 
			<div className="form">
				<p>Model #: {window.device.info.model}</p>
				<p>Firmware #: {window.device.info.firmware}</p>
			</div>
		;


		/* 
			raw data
			this format is only assumed - dummy data
			will have to redo this logic to filter the actual format
		*/
		var aps = [
			{
				ssid:"luxul-2G",
				band: "2.5GHz",
				security: "WPA2",
				encryption: "AES",
				guest: "No"
			},
			{
				ssid:"luxul-5G",
				band: "5GHz",
				security: "WPA2",
				encryption: "AES",
				guest: "No"
			}
		];
		var Rows = [];
		for (var i = 0; i < aps.length; i++) {
			var n = i+1;
			var odd = n%2;
			Rows.push(
			      <div className="formSection withHr">
					<h4>{aps[i].ssid}</h4>
					<fieldset className="info">
						<label className="">SSID:</label> <span>{aps[i].ssid}</span>
					</fieldset>
					<fieldset className="info">
						<label className="">Band:</label> <span>{aps[i].band}</span>
					</fieldset>
					<fieldset className="info">
						<label className="">Security:</label> <span>{aps[i].security}</span>
					</fieldset>
					<fieldset className="info">
						<label className="">Encryption:</label> <span>{aps[i].encryption}</span>
					</fieldset>
					<fieldset className="info">
						<label className="">Guest:</label> <span>{aps[i].guest}</span>
					</fieldset>
					<fieldset className="info">
						<label className=""></label> <span>

							<button className="primary"><b>Modify</b></button>
							<button className=""><b>Delete</b></button>

						</span>
					</fieldset>
			      </div>
			);
		}


		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>

					<LuxulForm stateScope={this}>

						<div className="formSection">
							<fieldset className="info">
								<label className="">Profile Name:</label> <span>luxul</span>
							</fieldset>
						</div>


						
					      <div className="formSection withHr">
							<h4>Unassigned APs:</h4>

							<fieldset className="LuxulRows">
								<div>
								<table>
								<tr>
								<td>
									<input type="checkbox" />
									<img src="/images/product-icons/ap/XAP-1510.png" />
									<span className="textValign">XAP-1510 87:10</span>
									<button className="primary circle small"><b><span className="fontIcon icon-ui_edit" /></b></button> 
								</td>
								</tr>
								</table>
								</div>
							</fieldset>

							<fieldset className="info">
								<label className=""></label> <span>
									<button className="warn small"><b><span className="fontIcon icon-arrow-down2" /> <span>Assign</span></b></button>
								</span>
							</fieldset>
					      </div>

						
					      <div className="formSection withHr">
							<h4>Assigned APs:</h4>
							<fieldset className="info">
								<label className=""></label> <span>
									<button className="warn small"><b><span className="fontIcon icon-arrow-up2" /> <span>Remove</span></b></button>
								</span>
							</fieldset>
					      </div>

						
					    {/*  <div className="formSection withHr">
							<fieldset className="info">
								<button className="primary circle"><b><span className="fontIcon icon-ui_edit" /></b></button> &nbsp; 
								<button className="delete circle"><b><span className="fontIcon icon-pk_delete" /></b></button>
							</fieldset>
					      </div>*/}


						<p>&nbsp;</p>

					</LuxulForm>
										
				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


